
.user__wrapper,
.self__perms {
  margin-top: 30px;
}
.user__wrapper {
  transition: all 0.3s ease-in;
  opacity: 0;
}
.user__wrapper.visible {
    opacity: 1;
}
.pushline label {
  font-size: 20px;
  font-weight: bold;
}
.pushline input {
  border-radius: 0;
  background: rgba(49, 49, 49, 0.2);
  border: none;
  color: #333;
  font-weight: bold;
}
.self__perms h3 {
  margin-bottom: 50px;
}
.self__perms .perm__item {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  border-radius: 5px;
}
.self__perms .perm__item .perm__title {
    font-weight: bold;
    font-size: 16px;
}
.self__perms .perm__item span {
    text-align: center;
    display: block;
}
.self__perms .movie__card {
  margin-bottom: 10px;
  box-shadow: 10px 9px 19px 0px rgba(66, 66, 66, 0.2);
}
.self__perms .movie__card .lazy {
    padding-bottom: 90%;
}
.self__perms .perm {
  position: relative;
  margin-bottom: 20px;
}
.self__perms .perm_sheet {
  position: absolute;
  top: 50%;
  left: 50%;
  color: #fff;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0 15px;
}
.user__infos {
  display: table;
  margin-bottom: 30px;
}
.user__infos p {
    margin: 0;
    font-size: 18px;
}
.user__infos p span {
      font-family: 'InconsolataBold', monospace;
      font-weight: bold;
      width: 70px;
      display: block;
      float: left;
}
.user__infos .user__edit {
    border-radius: 0;
    margin-top: 5px;
    position: relative;
    font-size: 18px;
    transition: 0.3s;
    padding: 10px 30px;
    z-index: 1;
    outline: none;
    shadow: inset 0 0 0 2px #000;
}
.user__infos .user__edit:hover {
      color: #fff;
      border-color: #fff;
}
.user__infos .user__edit:hover:before {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
        opacity: 1;
}
.user__infos .user__edit:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: -1;
      -webkit-transform: translate(-50%, -50%) scale(0);
              transform: translate(-50%, -50%) scale(0);
      width: 100%;
      height: 100%;
      background: #000;
      opacity: 00;
      transition: 0.3s;
}



