
@import url(../assets/fonts/font-face.css);
* {
  font-family: 'inconsolataRegular', monospace;
}
h1,
h2,
h3,
h4 {
  font-family: 'inconsolataBold', monospace;
}
.home__announce {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 20px;
  margin-bottom: 20px;
  z-index: 0;
}
.home__announce p {
    margin-left: 10px;
    font-family: 'inconsolataBold', monospace;
    margin-bottom: 0;
}
.home__announce:before {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 40px;
    z-index: -1;
    background: #f3f3f3;
    width: 90%;
    height: 20px;
}
.timeline__schedule {
  margin-top: 70px;
}
.movie__planning {
  position: relative;
  margin-top: 40px;
  display: table;
}
.movie__planning h2 {
    text-align: left;
    position: absolute;
    font-weight: bold;
    right: -15px;
    top: -50px;
    z-index: 999;
    opacity: 0.9;
}
.slide__desc {
  position: absolute;
  max-width: 400px;
  width: 100%;
  bottom: 10%;
  right: 0;
  z-index: 9;
  background: rgba(255, 255, 255, 0.8);
  padding: 10px 40px 10px 15px;
}
.slide__desc h3 {
    font-weight: bold;
}
.slide__desc h3 span {
      font-size: 18px;
      font-weight: lighter;
}
.slide__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  top: 0;
  left: 0;
  z-index: 1;
}
.goTo {
  color: #333;
  padding: 10px 15px;
  position: relative;
  display: table;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  z-index: 9;
  text-transform: uppercase;
  transition: 0.3s;
}
.goTo:hover:before {
    width: 90%;
}
.goTo:before {
    content: '';
    background: #fff;
    position: absolute;
    top: 20px;
    width: 0;
    right: -5px;
    opacity: 0.95;
    z-index: -1;
    transition: 0.3s;
    height: 20px;
}
.links__list {
  position: absolute;
  width: 100%;
  top: -30px;
}
.movie__list {
  padding-top: 50px;
  margin-top: 70px;
}
.links__dir {
  padding: 15px 20px;
  text-align: center;
  border-radius: 2px;
  background: #8492A6;
  color: #D3DCE6;
  width: 100%;
  font-size: 16px;
  display: table;
}
.main__head {
  margin-top: 20px;
}
.main__title {
  font-family: 'chaplin';
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}
.subtitle {
  color: #fff;
  text-align: center;
}
@media screen and (max-width: 768px) {
.el-carousel {
    display: none;
}
}
.el-carousel__container {
  position: relative;
  padding-bottom: 50%;
}
.el-carousel__item {
  opacity: 0.5;
}
.el-carousel__item img {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.el-carousel__item.is-active {
    opacity: 1;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}



