
.movie__card {
  overflow: hidden;
  position: relative;
  margin-bottom: 20px;
  border-radius: 5px;
}
.movie__card:hover .caption .image {
    -webkit-transform: translate(-50%, -50%) scale(1.4);
            transform: translate(-50%, -50%) scale(1.4);
    opacity: 0.8;
    border-radius: 5px;
}
.movie__card .caption {
    overflow: hidden;
    position: relative;
    display: block;
}



