
.ch-footer {
  margin-top: 50px;
  padding: 0 40px;
}
.map {
  position: relative;
}
@media screen and (max-width: 768px) {
.ns_container label {
    width: 100% !important;
}
.ns_container .el-form-item__content {
    margin: 0 !important;
}
}
.newsletter label {
  font-weight: bold;
}
.newsletter input {
  border-radius: 0;
  background: rgba(49, 49, 49, 0.2);
  border: none;
  color: #333;
  font-weight: bold;
}
.info_cine {
  background: rgba(255, 255, 255, 0.8);
  display: table;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 25px;
  padding: 20px;
}
@media screen and (max-width: 768px) {
.info_cine {
      right: 0;
}
}
.info_cine h4 {
    margin: 0;
}
.info_cine .subtle {
    opacity: 0.8;
}
.info_cine .contact__mail {
    margin: 0;
}
.deep_foot {
  margin: 20px 0;
  text-align: center;
}
.deep_foot .nb__visitors {
    font-family: 'inconsolataBold', monospace;
    font-size: 24px;
}
.deep_foot .m__legals {
    font-weight: bold;
    opacity: 0.8;
    cursor: pointer;
}



