
* {
  font-family: 'inconsolataRegular', monospace;
}
.gallery,
.related__movies,
.timesheet {
  margin: 70px 0 50px;
}
.gallery h3,
  .related__movies h3,
  .timesheet h3 {
    text-align: center;
    margin-bottom: 50px;
}
.gallery .movie__gallery {
  box-shadow: 10px 9px 19px 0px rgba(66, 66, 66, 0.2);
  border-radius: 5px;
}
.related__movies .movie__card {
  box-shadow: 10px 9px 19px 0px rgba(66, 66, 66, 0.2);
}
.timesheet__item {
  font-size: 28px;
  font-weight: bolder;
  max-width: 600px;
  margin: 0 auto;
  width: 100%;
}
@media screen and (max-width: 768px) {
.timesheet__item {
      font-size: 26px;
      text-align: center;
}
}
.timesheet__item p {
    display: inline-block;
    text-align: center;
    font-family: 'inconsolataBold', monospace;
    margin: 0;
}
@media screen and (min-width: 768px) {
.timesheet__date {
    width: 350px;
}
}
.timesheet__time, .timesheet__dubbing {
  opacity: 0.6;
}
.viewTrailer__mobile {
  margin: 10px;
}
.viewTrailer__mobile button {
    width: 100%;
    display: block;
}
@media screen and (min-width: 768px) {
.viewTrailer__mobile {
      display: none;
}
}
.edit__button {
  display: block;
  margin-top: -1px;
}
.edit__button button {
    width: 100%;
    border-radius: 0;
}
.movie__details {
  margin-bottom: 30px;
}
@media screen and (min-width: 768px) {
.movie__details {
      border-left: 1px solid rgba(170, 170, 170, 0.44);
      padding-left: 15px;
      margin: 0;
}
}
.player__button {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(1.2);
          transform: translate(-50%, -50%) scale(1.2);
  transition: 0.3s ease-out;
  z-index: 99;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid #fff;
}
.player__button .player__icon {
    color: #fff;
    position: absolute;
    top: 50%;
    font-size: 24px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.movie__trailer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.movie__trailer .yt__frame {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
@media screen and (max-width: 768px) {
.movie__trailer {
      display: none;
}
}
.background__details {
  position: absolute;
  bottom: 8%;
  left: 28%;
  color: #fff;
  z-index: 1;
}
.background__details h4 {
    color: #fff;
    font-weight: bold;
    margin-bottom: 10px;
}
.background__details p {
    font-weight: bold;
}
@media screen and (max-width: 768px) {
.background__details {
      position: initial;
      color: #333;
}
.background__details h4 {
        color: #333;
        font-size: 25px;
}
.background__details p {
        color: #333;
}
}
.movie__release {
  margin: 0;
}
.movie__title {
  margin: 0;
  line-height: 1;
}
.movie__duration {
  font-size: 16px;
}
.movie__genre {
  opacity: 0.8;
  font-size: 16px;
}
.movie__options {
  opacity: 0.8;
  font-size: 16px;
}
.movie__authors, .movie__actors {
  padding: 0;
}
.movie__authors li, .movie__actors li {
    display: inline-block;
    margin-right: 5px;
    opacity: 0.6;
}
.movie__authors li:first-child, .movie__actors li:first-child {
      font-weight: bold;
      opacity: 0.8;
}
.movie__gallery {
  position: relative;
  overflow: hidden;
  margin-bottom: 15px;
}
.movie__gallery img {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-60%, -50%);
            transform: translate(-60%, -50%);
    transition: 0.3s;
}
.movie__gallery:hover img {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.wrapper__details {
  padding-top: 15px;
  -webkit-transform: translateY(-40px);
          transform: translateY(-40px);
  background: #fff;
  position: relative;
  z-index: 999;
  transition: 0.3s ease-out;
}
.wrapper__details.slide-down {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.wrapper__details.slide-down .cover {
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
@media screen and (max-width: 768px) {
.wrapper__details {
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
}
.main__desc .cover {
  -webkit-transform: translateY(-120px);
          transform: translateY(-120px);
  transition: 0.3s ease-out;
}
@media screen and (max-width: 768px) {
.main__desc .cover {
      -webkit-transform: translateY(0);
              transform: translateY(0);
}
}
.main__desc .cover .lazy {
    border-radius: 0;
}
@media screen and (min-width: 768px) {
.background__cover {
    padding-bottom: 50%;
    overflow: hidden;
    position: relative;
    height: 0;
    cursor: pointer;
}
}
.background__cover:hover img {
  -webkit-transform: translate(-50%, -50%) scale(1.05) !important;
          transform: translate(-50%, -50%) scale(1.05) !important;
}
.background__cover:hover .player__button {
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
}
.background__cover__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  z-index: 1;
}
@media screen and (max-width: 768px) {
.background__cover__overlay {
      display: none;
}
}
@media screen and (max-width: 768px) {
.background__cover .lazy__bg {
    display: none;
}
}
.background__cover img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(1.1);
          transform: translate(-50%, -50%) scale(1.1);
  opacity: 0.8;
  transition: 0.3s ease-out;
}
@media screen and (max-width: 768px) {
.background__cover img {
      display: none;
}
}



