
@import url(../../../assets/fonts/font-face.css);
.cls-1,
.cls-2,
.cls-5,
.cls-6,
.cls-7 {
  fill: none;
}
.cls-1,
.cls-2 {
  stroke: #000;
}
.cls-1,
.cls-5,
.cls-7 {
  stroke-linecap: round;
}
.cls-1,
.cls-2,
.cls-5,
.cls-7 {
  stroke-miterlimit: 10;
}
.cls-1 {
  stroke-dasharray: 11.99 11.99;
}
.cls-2 {
  stroke-width: 5px;
}
.cls-3 {
  opacity: 0.22;
  isolation: isolate;
}
.cls-10,
.cls-4,
.cls-8,
.cls-9 {
  fill: #fff;
}
.cls-5,
.cls-6,
.cls-7 {
  stroke: #fff;
}
.cls-5 {
  stroke-width: 0.5px;
}
.cls-8,
.cls-9 {
  font-size: 8px;
  font-family: "Mistral", sans-serif;
}
.cls-9 {
  white-space: pre;
}
.cls-10 {
  font-size: 3px;
  font-family: Myriad Pro;
  font-weight: 700;
}
.preloader {
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100vh;
  background: #fff;
  left: 0;
  top: 0;
  transition: 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.preloader .preloader-svg {
    position: fixed;
    max-width: 500px;
    width: 100%;
    padding: 50px;
    top: 50%;
    left: 50%;
    transition: 0.5s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}
.preloader.hidden {
    opacity: 0;
    top: -50px;
    visibility: hidden;
    pointer-events: none;
}
.preloader.hidden .preloader-svg {
      top: 40%;
      -webkit-transform: translate(-50%, -50%) scale(0);
              transform: translate(-50%, -50%) scale(0);
}
#clapy {
  -webkit-animation: mymove 1s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* Safari 4.0 - 8.0 */
  animation: mymove 1s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.cls-1 {
  -webkit-animation: rotation 2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
  /* Safari 4.0 - 8.0 */
  animation: rotation 2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes mymove {
0% {
    -webkit-transform: rotate(21deg);
            transform: rotate(21deg);
    -webkit-transform-origin: 23px 33px;
            transform-origin: 23px 33px;
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transform-origin: 23px 33px;
            transform-origin: 23px 33px;
}
100% {
    -webkit-transform: rotate(21deg);
            transform: rotate(21deg);
    -webkit-transform-origin: 23px 33px;
            transform-origin: 23px 33px;
}
}

/* Standard syntax */
@keyframes mymove {
0% {
    -webkit-transform: rotate(21deg);
            transform: rotate(21deg);
    -webkit-transform-origin: 23px 33px;
            transform-origin: 23px 33px;
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transform-origin: 23px 33px;
            transform-origin: 23px 33px;
}
100% {
    -webkit-transform: rotate(21deg);
            transform: rotate(21deg);
    -webkit-transform-origin: 23px 33px;
            transform-origin: 23px 33px;
}
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes rotation {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transform-origin: 42.5px 42.5px;
            transform-origin: 42.5px 42.5px;
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-transform-origin: 42.5px 42.5px;
            transform-origin: 42.5px 42.5px;
}
}

/* Standard syntax */
@keyframes rotation {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transform-origin: 42.5px 42.5px;
            transform-origin: 42.5px 42.5px;
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-transform-origin: 42.5px 42.5px;
            transform-origin: 42.5px 42.5px;
}
}



