
.proposal__movie {
  border-radius: 0;
  color: #333;
  margin: 30px auto;
  position: relative;
  display: table;
  font-size: 18px;
  transition: 0.3s;
  padding: 10px 55px;
  z-index: 1;
  outline: none;
  box-shadow: inset 0 0 0 2px #000;
}
.proposal__movie:hover {
    color: #fff;
}
.proposal__movie:hover:before {
      -webkit-transform: translate(-50%, -50%) scale(1);
              transform: translate(-50%, -50%) scale(1);
      opacity: 1;
}
.proposal__movie:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;
    -webkit-transform: translate(-50%, -50%) scale(0);
            transform: translate(-50%, -50%) scale(0);
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 00;
    transition: 0.3s;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 400px;
  background: #fbfdff;
}
.el-upload-list--picture-card .el-upload-list__item-thumbnail {
  width: 200%;
  height: initial;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  position: absolute;
  line-height: 178px;
  text-align: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.avatar {
  width: 100%;
  display: block;
  border-radius: 5px;
}
.form-add-proposal {
  border-radius: 0px;
  padding: 15px;
}



